"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";
import StatsGrid from "@/components/admin/StatsGrid";
import SubscriptionPie from "@/components/admin/SubscriptionPie";
import RecentStoresTable from "@/components/admin/RecentStoresTable";
import { Loader2 } from "lucide-react";

export default function AdminDashboardPage() {
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState<any>(null);

  useEffect(() => {
    async function loadStats() {
      const { data, error } = await supabase.rpc('get_platform_stats');
      if (!error) setStats(data);
      setLoading(false);
    }
    loadStats();
  }, []);

  if (loading) return <div className="h-full flex items-center justify-center"><Loader2 className="animate-spin text-emerald-500"/></div>;

  return (
    <div className="space-y-8">
      <div>
        <h2 className="text-2xl font-bold text-white">Platform Overview</h2>
        <p className="text-gray-400">Live data from across the StoreLink ecosystem.</p>
      </div>

      <StatsGrid stats={stats} />

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        <div className="lg:col-span-2 bg-gray-900 border border-gray-800 rounded-2xl p-6 min-h-[300px]">
           <h3 className="font-bold text-gray-200 mb-4">Subscription Distribution</h3>
           <SubscriptionPie stats={stats} />
        </div>

        <div className="bg-gray-900 border border-gray-800 rounded-2xl p-6">
           <h3 className="font-bold text-gray-200 mb-4">Recent Stores</h3>
           <RecentStoresTable />
        </div>
      </div>
    </div>
  );
}