"use client";
import { PieChart, Pie, Cell, ResponsiveContainer, Tooltip, Legend } from 'recharts';

const COLORS = ['#374151', '#3B82F6', '#8B5CF6']; // Gray (Free), Blue (Prem), Purple (Dia)

export default function SubscriptionPie({ stats }: { stats: any }) {
  const data = [
    { name: 'Free Plan', value: stats?.free_count || 0 },
    { name: 'Premium', value: stats?.premium_count || 0 },
    { name: 'Diamond', value: stats?.diamond_count || 0 },
  ];

  return (
    <div className="h-[250px] w-full">
      <ResponsiveContainer>
        <PieChart>
          <Pie
            data={data}
            cx="50%"
            cy="50%"
            innerRadius={60}
            outerRadius={80}
            paddingAngle={5}
            dataKey="value"
          >
            {data.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
          <Tooltip 
             contentStyle={{ backgroundColor: '#111827', borderColor: '#374151', borderRadius: '8px' }}
             itemStyle={{ color: '#fff' }}
          />
          <Legend verticalAlign="bottom" height={36}/>
        </PieChart>
      </ResponsiveContainer>
    </div>
  );
}