import { Users, ShoppingBag, DollarSign, TrendingUp } from "lucide-react";

export default function StatsGrid({ stats }: { stats: any }) {
  const items = [
    { label: "Total Stores", value: stats?.total_stores, icon: Users, color: "text-blue-400" },
    { label: "Total Products", value: stats?.total_products, icon: ShoppingBag, color: "text-purple-400" },
    { label: "Orders", value: stats?.total_orders, icon: TrendingUp, color: "text-yellow-400" },
    { label: "Revenue (GMV)", value: `â‚¦${stats?.total_revenue?.toLocaleString()}`, icon: DollarSign, color: "text-emerald-400" },
  ];

  return (
    <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
      {items.map((item, idx) => (
        <div key={idx} className="bg-gray-800/50 border border-gray-800 p-6 rounded-2xl hover:border-gray-700 transition">
          <div className="flex items-center justify-between mb-4">
            <span className="text-gray-400 text-sm font-medium">{item.label}</span>
            <item.icon className={item.color} size={20} />
          </div>
          <p className="text-3xl font-extrabold text-white">{item.value || 0}</p>
        </div>
      ))}
    </div>
  );
}